	module.exports = {"data":{"wordpressPost":{"title":"Top 10 Most Read in December: Death, injury and disability from kinetic impact projectiles, Clinical trial transparency of Big Pharma and the relationship between PHC physician specialisation and hospitalisation in Brazil.","content":"<p>December saw four new papers enter into BMJ Open’s top 10 most read articles. In first place is a systematic review of deaths, injuries and permanent disability from rubber and plastic bullets and other projectiles used in crowd-control settings between 1990 and 2017. The authors conclude that rubber/plastic bullets used for crowd control can cause significant morbidity and mortality, and given their inherent inaccuracy, they shouldn’t be used for this purpose.</p>\n<p>Also new this month is a study by Jennifer Miller and colleagues who examined the clinical trial transparency of large pharmaceutical and biotechnology companies and their 2014 FDA-approved drugs. The authors found high levels of transparency overall, although opportunities for improvement remained including the transparency of results from trials that are not conducted in patients, such as trials in healthy volunteers.</p>\n<p>Other new entries include a Brazilian study assessing the association between primary health care model/ physician specialisation and hospitalisation for ambulatory care-sensitive conditions (number 6) and a cross-sectional assessment of appropriate methods for assessing mobility function in patients with visual field loss (number 9).</p>\n<table>\n<tbody>\n<tr>\n<td width=\"45\"><strong>Rank</strong></td>\n<td width=\"170\"><strong>Author(s)</strong></td>\n<td width=\"401\"><strong>Title</strong></td>\n</tr>\n<tr>\n<td width=\"45\">1</td>\n<td width=\"170\"><em>Haar et al.</em></td>\n<td width=\"401\"><a href=\"http://bmjopen.bmj.com/content/7/12/e018154\">Death, injury and disability from kinetic impact projectiles in crowd-control settings: a systematic review</a></td>\n</tr>\n<tr>\n<td width=\"45\">2</td>\n<td width=\"170\"><em>Ashton et al. </em></td>\n<td width=\"401\"><a href=\"http://bmjopen.bmj.com/content/7/10/e016089\">Do emotions related to alcohol consumption differ by alcohol type? An international cross-sectional survey of emotions associated with alcohol consumption and influence on drink choice in different settings</a></td>\n</tr>\n<tr>\n<td width=\"45\">3</td>\n<td width=\"170\"><em>Watkins et al. </em></td>\n<td width=\"401\"><a href=\"http://bmjopen.bmj.com/content/7/11/e017722\">Effects of health and social care spending constraints on mortality in England: a time trend analysis</a></td>\n</tr>\n<tr>\n<td width=\"45\">4</td>\n<td width=\"170\"><em>Xie et al.</em></td>\n<td width=\"401\"><a href=\"http://bmjopen.bmj.com/content/7/6/e015735\">Risk of death among users of Proton Pump Inhibitors: a longitudinal observational cohort study of United States veterans</a></td>\n</tr>\n<tr>\n<td width=\"45\">5</td>\n<td width=\"170\"><em>Miller et al. </em></td>\n<td width=\"401\"><a href=\"http://bmjopen.bmj.com/content/7/12/e017917\">Measuring clinical trial transparency: an empirical analysis of newly approved drugs and large pharmaceutical companies</a></td>\n</tr>\n<tr>\n<td width=\"45\">6</td>\n<td width=\"170\"><em>Afonso et al. </em></td>\n<td width=\"401\"><a href=\"http://bmjopen.bmj.com/content/7/12/e015322\">Association between hospitalisation for ambulatory care-sensitive conditions and primary health care physician specialisation: a cross-sectional ecological study in Curitiba (Brazil)</a></td>\n</tr>\n<tr>\n<td width=\"45\">7</td>\n<td width=\"170\"><em>Irving et al. </em></td>\n<td width=\"401\"><a href=\"http://bmjopen.bmj.com/content/7/10/e017902\">International variations in primary care physician consultation time: a systematic review of 67 countries</a></td>\n</tr>\n<tr>\n<td width=\"45\">8</td>\n<td width=\"170\"><em>Graham et al. </em></td>\n<td width=\"401\"><a href=\"http://bmjopen.bmj.com/content/7/9/e016942\">What factors are associated with reporting lacking interest in sex and how do these vary by gender? Findings from the third British national survey of sexual attitudes and lifestyles</a></td>\n</tr>\n<tr>\n<td width=\"45\">9</td>\n<td width=\"170\"><em>Subhi et al. </em></td>\n<td width=\"401\"><a href=\"http://bmjopen.bmj.com/content/7/11/e018831\">Functional visual fields: a cross-sectional UK study to determine which visual field paradigms best reflect difficulty with mobility function</a></td>\n</tr>\n<tr>\n<td width=\"45\">10</td>\n<td width=\"170\"><em>Hives et al.</em></td>\n<td width=\"401\"><a href=\"http://bmjopen.bmj.com/content/7/11/e017521\">Can physical assessment techniques aid diagnosis in people with chronic fatigue syndrome/myalgic encephalomyelitis? A diagnostic accuracy study</a></td>\n</tr>\n</tbody>\n</table>\n<p>&nbsp;</p>\n<p><em>Most read figures are based on pdf downloads and full text views. Abstract views are excluded.</em><!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"e6a41147-e224-510e-af7e-f2182cadb473"}}

